@import "../../../..//../../css-config/mixins.scss";

.col_main{
    background-color:transparent;
  


    .col_details{
        max-height: fit-content;
        display: flex;
        justify-content: center;
        gap: 30px;
        padding: 0 10px;
       
        flex-flow: wrap;
        
        @include md{
            padding: 20px 30px;
        }

        .col1{
            height: 500px;
            display: flex;
            justify-content: center;
            object-fit: contain;
           width: 500px;
           @include md{
            width: 500px;
            
           }
           
        

         

            img{
                width: 100%;
            }
            
        }

        .col2{

            display: flex;
            gap: 20px;
            flex-direction: column;
            width: 100%;
          padding: 0 10px;
          @include md{
            padding: 0 20px;
            width: 50%;
          }

            .button{
                display: flex;
               justify-content: center;
                @include md{
                    justify-content: flex-end;
                }
               
            }

            .overview{
                display: flex;
               
                gap: 20px;
                flex-direction: column;
                padding: 20px 0;
                @include md{
                    padding: 0 0;

                }

                h1{
                  
                    
                    color: rgba(0, 0, 255, 0.731);
                    font-size: 22px;
                    @include md{
                        font-size: 27px;
                    }
                   
                }
                p{
                    color: rgba(0,0,0,0.6);
                    text-transform: lowercase;
                   word-spacing: 6px;
                   text-align: justify;
                }
            }

            .modules{
                display: flex;
                flex-direction: column;
                gap: 20px;

                h3{
                    color: rgba(0, 0, 255, 0.731);
                    font-weight: 700;
                    font-size: 19px;
                    
                    @include md{
                        font-size: 24px;
                    }
                }


                ul{
                    display: flex;
                    flex-direction: column;
                    gap: 10px;
                   
                   

                    li{
                       
                        text-transform: capitalize;
                        padding: 20px 0;
                        
                        color: rgba(0,0,0,0.6);
                        font-size: 12px;
                        @include md{
                            font-size: 18px;
                        }
                    }
                }
            }
        }
        
    }
    .slider{
        height: 80px;
        width: 100%;
    }
}